import React, { Component } from 'react';
import{ connect } from 'react-redux';
import {selectBook} from '../actions/index';
import {bindActionCreators} from 'redux';

 class BookList extends Component {

  renderList(){
    return this.props.books.map((book,index) =>{
      return(
        <li key={index} onClick={()=> this.props.selectBook(book)} className="list-group-item">{book.title}</li>
      )
    })
  }

  render() {
    return (
      <div className="list">
        <div className="col-sm-4"></div>
        <ul className="list-group col-sm-4">
          {this.renderList()}
        </ul>
      </div>
    );
  }
}

function mapStateToProps(state){
  //whatever is returned will show up as mapStateToProps
  //inside of BookList
  return{
    books: state.books
  };
}
// Anything returned from this function will end up as mapStateToProps
// on the BookList container
function mapDispatchToProps(dispatch){

  //Whenever selectBook is called, the result should be passed
  //to all of our reducers
  return bindActionCreators({ selectBook:selectBook}, dispatch)
}
//Promote BookList from component to a container - it needs to know
// about this new dispatch method, selectBook. Make it available
//as a prop
export default connect(mapStateToProps, mapDispatchToProps)(BookList);



